// import ArticleDetail from '@/components/home/ArticleDetail.vue'
// import UserInfoEdit from '@/components/user/UserInfoEdit.vue'
// import Search from '@/components/search/Search.vue'
// import SearchResult from '@/components/search/SerachResult.vue'
// import Chat from '@/components/user/Chat.vue'

// 路由懒加载
const Login = () => import('@/components/login/Login.vue')
const Layout = () => import('@/components/home/Layout.vue')
const Home = () => import('@/components/home/Home.vue')
const User = () => import('@/components/user/User.vue')
const ArticleDetail = () => import('@/components/home/ArticleDetail.vue')
const UserInfoEdit = () => import('@/components/user/UserInfoEdit.vue')
const Search = () => import('@/components/search/Search.vue')
const SearchResult = () => import('@/components/search/SerachResult.vue')
const Chat = () => import('@/components/user/Chat.vue')

// 问题：之前退出登录后重新登录时报错：Uncaught (in promise) NavigationDuplicated: Avoided redundant navigation to current location: "/layout/home".
// 原因：
// 解决：重写 Router 上的 push 方法，对其使用 catch 捕捉异常
const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  {
    path: '/layout',
    component: Layout,
    redirect: '/layout/home',
    children: [
      // 子路由是不加斜线的
      { path: 'home', component: Home },
      { path: 'user', component: User }
    ]
  },
  { path: '/article_detail', component: ArticleDetail },
  { path: '/userinfo_edit', component: UserInfoEdit },
  { path: '/search', component: Search },
  { path: '/search_result/:keywords', component: SearchResult },
  { path: '/chat', component: Chat }
]

const router = new VueRouter({
  routes
})

// 前置守卫：在路由跳转之前，执行此函数，判断是否跳转
router.beforeEach((to, from, next) => {
  if (localStorage.getItem('token')?.length > 0 && to.path === '/login') {
    // 阻止跳转到登录页
    // 但存在一个问题：当token过期的时候，本地也是有token的，且需要去登录页，那就可以在响应拦截器里把token情况，这样就可以跳转了
    // next(false)存在一个问题，如果我跳转到登录页停在原地不动的话，会导致用户无法操作后续步骤,甚至想返回都返回不了，但其实移动端不需要考虑这个问题，因为手机端是无法通过修改网址来跳转路由的，即无法破坏路由规则，方案1：强制跳转回到首页
    // next(false)
    next('/layout/home')
  } else {
    next()
  }
})
export default router
