import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/utils/connsole.js'

// ————自己配置的部分————
import 'amfe-flexible'

// 按需导入组件
import { NavBar, Form, Field, Button, Tabbar, TabbarItem, Icon, Tab, Tabs, Cell, CellGroup, List, PullRefresh, ActionSheet, Divider, Badge, ShareSheet, Tag, Image, Dialog, DatetimePicker, Popup, Search, Image as VanImage, Loading } from 'vant'
// 按需挂载组件
Vue.use(NavBar)
Vue.use(Form)
Vue.use(Field)
Vue.use(Button)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Icon)
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Cell)
Vue.use(CellGroup)
Vue.use(List)
Vue.use(PullRefresh)
Vue.use(ActionSheet)
Vue.use(Divider)
Vue.use(Badge)
Vue.use(ShareSheet)
Vue.use(Tag)
Vue.use(Image)
Vue.use(Dialog)
Vue.use(DatetimePicker)
Vue.use(Popup)
Vue.use(Search)
Vue.use(VanImage)
Vue.use(Loading)
// 封装中间件函数
const directiveObj = {
  install(Vue) {
    Vue.directive('fofo', {
      // inserted指令所在标签被插入到bom中才触发，而你那个对话框只有第一次被插入了，后面都是控制的显示与隐藏
      // el代表指令所在的标签
      inserted(el) {
        // 指令所在van-search组件
        // 因为这个自定义指令在很多地方都有使用，所以el的标签类型是不一样的，在搜索组件内el代表div，在评论组件内代表的是textarea,，所以必须要判断el的标签类型，否则获取不到input
        if (el.nodeName === 'TEXTAREA' || el.nodeName === 'INPUT') {
          el.focus()
        } else {
          const theInput = el.querySelector('input')
          const theTextArea = el.querySelector('textarea')
          if (theInput) theInput.focus()
          if (theTextArea) theTextArea.focus()
        }
      },
      // update是在标签更新时触发
      update(el) {
        if (el.nodeName === 'TEXTAREA' || el.nodeName === 'INPUT') {
          el.focus()
        } else {
          const theInput = el.querySelector('input')
          const theTextArea = el.querySelector('textarea')
          if (theInput) theInput.focus()
          if (theTextArea) theTextArea.focus()
        }
      }
    })
  }
}
// 执行目标对象里install方法并传到vue里
Vue.use(directiveObj)

Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
