import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userPhoto: '',
    userName: ''
  },
  getters: {
  },
  mutations: {
    SET_USERPHOTO(state, value) {
      state.userPhoto = value
    },
    SET_USERNAME(state, value) {
      state.userName = value
    }
  },
  actions: {
  },
  modules: {
  }
})
