<template>
  <div>
    <!-- 组件缓存 -->
    <!-- 但是组件缓存存在一个问题：如果你将所有的组件都缓存起来的话，会导致你进入一些变化的页码时无法更新新的内容，原因：一旦组件缓存了，即不会被销毁，既然没有被销毁，自然不会执行created函数，所以，一定不要缓存变化的组件，通过exclude或者include来解决 -->
    <keep-alive exclude="Search,SearchResult,ArticleDetail,UserInfoEdit">
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {}
</script>

<style lang="less" scoped></style>
